.teacher {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding: 39px 21px 20px 50px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding: 50px 43px 40px 50px;
  }

  @media screen and (min-width: 1280px) {
    padding: 130px 183px 50px;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-left: -109px;
  }
}

.teacher__title {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-bottom: 20px;
    font-weight: 800;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.03em;
    font-family: getFonts('main');
    margin-top: 0;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 38px;
    font-style: normal;
    font-weight: 800;
    font-size: 26px;
    line-height: 130%;
    letter-spacing: 0.03em;
    font-family: getFonts('main');
    margin-top: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 60px;
    font-family: getFonts('main');
    font-style: normal;
    font-weight: 800;
    font-size: 46px;
    line-height: 130%;
    letter-spacing: 0.03em;
    margin-top: 0;
  }
}

.teacher-wrapper {
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    display: flex;
    justify-content: flex-start;
  }
  @media screen and (min-width: 1280px) {
    display: flex;
  }
}

.teacher__name {
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-right: 40px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 30px;
  }
}

.teacher__img {
  display: block;

  @media screen and (min-width: 320px) and (max-width: 1279px) {
    width: 243px;
    height: 306px;
  }

  @media screen and (min-width: 1280px) {
    width: 530px;
    height: 667px;
  }
}

.teacher-about {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-top: 20px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 401px;
    margin-top: -25px;
  }

  @media screen and (min-width: 1280px) {
    width: 640px;
  }
}

.teacher-about__name {
  font-family: getFonts('main');
  color: #111518;
  font-weight: bold;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 16px;
    margin-bottom: 10px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 20px;
    margin-bottom: 10px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    margin-bottom: 15px;
  }
}

.teacher-about__name::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #b9b9b9;
  opacity: 0.38;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-top: 10px;
    margin-bottom: 26px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-top: 16px;
    margin-bottom: 26px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 16px;
    margin-bottom: 42px;
  }
}

.teacher-about__experience {
  font-family: getFonts('main');
  color: #111518;
  font-weight: bold;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-bottom: 20px;
    font-size: 13px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 1.538;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 40px;
    font-size: 16.667px;
    line-height: 1.75;
  }
}

.teacher-about__experience::after {
  display: block;
  content: '';
  width: 25px;
  height: 2px;
  background-color: #ff6b09;
  margin-top: 22px;
  margin-left: -40px;
  @media screen and (min-width: 1280px) {
    margin-bottom: 36px;
    margin-right: 35px;
  }
}

.icon-list {
  list-style: none;
  position: relative;

  &::before {
    content: '';
    display: block;
    height: 30px;
    width: 30px;
    background-image: url('../images/check.svg');
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    left: -45px;
    top: 0px;
    fill: #ff6b09;
  }
}

.teacher-about__experience-item {
  padding-left: 18px;

  &:not(:last-child) {
    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin-bottom: 22px;
    }

    @media screen and (min-width: 768px) and (max-width: 1279px) {
      margin-bottom: 26px;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 30px;
    }
  }
}

.teacher-about__experience-text {
  font-family: getFonts('main');
  color: #111518;
  font-weight: bold;
  padding-left: 20px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 14px;
    line-height: 1.214;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 12px;
    line-height: 1.219;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16.667px;
    line-height: 1.218;
  }
}

.teacher-about__biography-text {
  font-family: getFonts('main');
  color: #b9b9b9;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    font-size: 11px;
    line-height: 1.8;
    margin: 0px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 10px;
    line-height: 2;
    margin: 0px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.55;
    margin-bottom: 20px;
  }
}

.teacher-about__certificate {
  display: block;
  font-family: getFonts('main');
  color: #ff6b09;
  text-decoration: underline;

  @media screen and (max-width: 480px) {
    font-size: 12px;
    line-height: 1.66;
  }

  @media screen and (min-width: 481px) {
    font-size: 12px;
    line-height: 1.66;
  }

  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.55;
  }
}
